.productCard
  background: var(--white)
  padding: 0 5px
  position: relative
  @media (min-width: 480px)
    padding: 0 12px
  @media (min-width: 768px)
    &:last-child
      display: none
  @media (min-width: 1000px)
    &:last-child
      display: block
  @media (min-width: 1440px)
    padding: 0 20px

.icon
  position: absolute
  top: 8px
  right: 8px
  @media (min-width: 480px)
    top: 10px
    right: 10px
  @media (min-width: 768px)
    top: 17px
    right: 17px
  @media (min-width: 1440px)
    top: 15px
    right: 15px
  svg
    width: 14px
    height: 12px
    @media (min-width: 480px)
      width: 20px
      height: 18px
    @media (min-width: 768px)
      width: 22px
      height: 20px
    @media (min-width: 1440px)
      width: 26px
      height: 24px

img
  display: block
  width: 100%
  height: 160px
  object-fit: contain
  @media (min-width: 480px)
    height: 270px
  @media (min-width: 768px)
    height: 300px
  @media (min-width: 1440px)
    height: 435px

.info
  display: flex
  justify-content: space-between
  gap: 6px
  padding: 10px 0
  border-top: 1px solid var(--taffy-pink)
  @media (min-width: 480px)
    gap: 10px
  @media (min-width: 768px)
    padding: 14px 0
    border-top: 2px solid var(--taffy-pink)
  @media (min-width: 1440px)
    padding: 25px 0

.title
  color: var(--umbra)
  font-size: 10px
  font-weight: 400
  @media (min-width: 480px)
    font-size: 12px
  @media (min-width: 768px)
    font-size: 14px
  @media (min-width: 1440px)
    font-size: 16px
    width: 58%

.price
  color: var(--taffy-pink)
  font-size: 10px
  font-weight: var(--bold-fw)
  white-space: nowrap
  @media (min-width: 480px)
    font-size: 14px
  @media (min-width: 768px)
    font-size: 16px
  @media (min-width: 1440px)
    font-size: 24px
